const imgSrc = 'data:image/png;base64,'

function SignInPage() {
    const doSignIn = (e) => {
        e.preventDefault();
        const loginURL = `${process.env.REACT_APP_API_URL}/main/login`;
        fetch(loginURL, {
            credentials: 'include',
            cache: 'no-cache'
        }).then(res => {
            return res.text();
        }).then((url) => {
            window.location = url;
        }).catch((err) => {
            console.log(err);
        });
    }
    return (
        <div onClick={doSignIn}>
            <img src={imgSrc} alt='' />
             
        </div>
    );
}
export default SignInPage;